
import { Injectable } from '@angular/core';
@Injectable({
    providedIn: 'root',
  })
export class adminServe {
    constructor(){
    }
    setEditMode(mode: string):void
    {
        localStorage.setItem("editmode", mode);
    }
    getEditMode():string
    {
        return localStorage.getItem("editmode");
    }
    
}