import { Component, OnInit } from '@angular/core';
import { HttpClient,HttpHeaders }  from "@angular/common/http";
import {webconfig,backendurl} from '../config/config';
import {user,user_result} from './user'
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { HttpErrorHandler, HandleError } from '../httpserve/http-error-handler.service';
const httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json'
    },
    )
  };

@Injectable({
    providedIn: 'root',
  })
export class userServe {
    URL:any;  // URL to web api
    private handleError: HandleError;
    constructor(private httpClient: HttpClient,httpErrorHandler: HttpErrorHandler){
        this.handleError = httpErrorHandler.createHandleError('userService');
        this.URL=backendurl.user

    }
    login(model: user): Observable<user_result> {
       return this.httpClient
        .post<user_result>(`${webconfig.backendurl}${this.URL.login}`,model,httpOptions)
        .pipe(
            catchError(this.handleError('login',{
                success:false,
                messages:"Connection failed",
                jwt:"",
                refjwt:""
            })))
        .pipe(
            map(response=>{
                if (response.success){
                    localStorage.setItem("accessToken",response.jwt);
                    localStorage.setItem("refreshToken",response.refjwt)
                }
                
                return response;
            })
        );}
    checkLoginstatus():Observable<any>{
        return this.httpClient
         .post<any>(`${webconfig.backendurl}${this.URL.refjwt}`,
         httpOptions )
        .pipe(
           catchError(this.handleError('login',false))
            )
    }
    logout():boolean{
        localStorage.clear();
        return true;
    }
    getAccessToken():string{
        return localStorage.getItem("accessToken")
    }
    getRefToken():string{
        return localStorage.getItem("refreshToken")
    }
    setAccessToken(token:string):void {
        localStorage.setItem("accessToken",token);

    }
    refreshToken():Observable<any> {
        var jwt:string=this.getRefToken();
        const httpOptionss = {
            headers: new HttpHeaders({
              'Content-Type':  'application/json',
              'Authorization':  `Bearer ${jwt}`,
            })
          };
        return this.httpClient.put<any>(`${webconfig.backendurl}${this.URL.refjwt}`,'',httpOptionss)
        // .pipe(
        //     map(response=>{
        //         if (response.success){
        //             localStorage.setItem("accessToken",response.token);
        //         }
        //         return response.success;
        //     }
        // ))
      }
}