import { Component, OnInit } from '@angular/core';
import {adminServe} from './admin.serve'
import { MatRadioChange } from '@angular/material';
@Component({
  selector: 'app-admin-panel',
  templateUrl: './admin-panel.component.html',
  styleUrls: ['./admin-panel.component.css']
})
export class AdminPanelComponent implements OnInit {
  editmode = 'close';
  constructor(private adminServeh: adminServe) { }

  ngOnInit() {
    var mode=this.adminServeh.getEditMode();
    if(['open', 'close'].indexOf(mode) >= 0){
      this.editmode=mode;
    }
  }
  editModeChange(event: MatRadioChange){
    this.adminServeh.setEditMode(event.value)
  }
}
