import { Injectable } from '@angular/core';
import {
  HttpEvent, HttpInterceptor, HttpHandler, HttpRequest, HttpErrorResponse
} from '@angular/common/http';

import { userServe } from '../user/user.serve';
import { throwError, BehaviorSubject, Observable } from 'rxjs';
import { catchError, map, switchMap, take, filter } from 'rxjs/operators';
@Injectable()
export class AuthInterceptor implements HttpInterceptor {

  constructor(private auth: userServe) {}

  intercept(req: HttpRequest<any>, next: HttpHandler):Observable<HttpEvent<any>>{
    // Get the auth token from the service.
    var authToken= this.auth.getAccessToken();
    if(req.headers.get('Authorization') == null){
      //authToken=req.headers.get('authorization').substring(7);
      req = this.addToken(req, authToken);
    }
    // else{
    //   authToken = this.auth.getAccessToken()
      
    // }
    // // console.log(authToken)
    // if (authToken==null) {
    //   return ;
    // }
    /*
    * The verbose way:
    // Clone the request and replace the original headers with
    // cloned headers, updated with the authorization.
    const authReq = req.clone({
      headers: req.headers.set('Authorization', authToken)
    });
    */
    // Clone the request and set the new header in one step.
    //const authReq = req.clone({ setHeaders: { Authorization: authToken } });

    // send cloned request with header to the next handler.

    return next.handle(req).pipe(catchError(error => {
      if (error instanceof HttpErrorResponse && error.status === 401) {
        //console.log(error)
        return this.handle401Error(req, next);
      } else {
        return throwError(error);
      }
    }));
  }
  private addToken(request: HttpRequest<any>, token: string) {
    //console.log(token)
    return request.clone({
      setHeaders: {
        'Authorization': `Bearer ${token}`
      }
    });
  }

private isRefreshing = false;
private refreshTokenSubject: BehaviorSubject<any> = new BehaviorSubject<any>(null);

private handle401Error(request: HttpRequest<any>, next: HttpHandler) {
  if (!this.isRefreshing) {
    this.isRefreshing = true;
    this.refreshTokenSubject.next(null);

    return this.auth.refreshToken().pipe(
      switchMap((response: any) => {
        //refresh token is invalid
        if(!response.success){
          return;
        }
        this.isRefreshing = false;
        this.auth.setAccessToken(response.token);
        this.refreshTokenSubject.next(response.token);
        return next.handle(this.addToken(request, response.token));
      }));

      } 
      else {
        return this.refreshTokenSubject.pipe(
          filter(token => token != null),
          take(1),
          switchMap(token => {
            return next.handle(this.addToken(request, token));
          }
        ));
      }
}
}


